import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
    // ---sessionid--if_login  
export default new Vuex.Store({
        state: {
            sessionid: GetCookie('sessionid') || '', //
            if_login: nowurl_if(), //
            trade_detail: {
                "qudao_uid": 0, //  userid
                "qudao_name": "", // 
                "mchnt_uid": 0, //  userid
                "mchnt_name": "", // 
                "trade_syssn": "", // 
                "trade_amt": 0, // , 
                "trade_fee": 0, // , 
                "trade_dtm": "", // 
                "trade_type": "", // 
                "trade_status": "", // 
                "trade_msg": "", // 
                "mchnt_mobile": "", // 
            }, //
            login_no: false, //session
            ad_url: location.host == 'qudao.qfpay.com' ? 'https://o.qfpay.com' : 'https://o.qa.qfpay.net',//online
        },
        mutations: {
            login(state) {
                //SetCookie('sessionid', 'yaya123344', 12);
                state.sessionid = GetCookie('sessionid');
            },
            logout(state) {
                //deleteCookie('sessionid');
                state.sessionid = GetCookie('sessionid');
            },
            login_ify(state) {
                state.if_login = true;
            },
            login_ifn(state) {
                state.if_login = false;
            },
            t_detail(state, obj) {
                state.trade_detail = obj;
                //console.log(state.trade_detail);
            },
            t_login_no(state, obj) {
                state.login_no = obj;
            },
        }
    })
    //
function nowurl_if() {
    let url = window.location.href.split('/');
    let len = url.length - 1;
    if (url[len] == 'login') {
        return true;
    } else {
        return false;
    }
}
//cookie
function GetCookie(cookieName) {
    var strCookie = document.cookie;
    // cookie/
    var arrCookie = strCookie.split("; ");
    for (var i = 0; i < arrCookie.length; i++) {
        // cookiecookie
        var arr = arrCookie[i].split("=");
        // cookieNamecookie
        if (arr[0] == cookieName)
            return arr[1];
    }
    return "";
}
//cookie
function deleteCookie(username) {
    var date = new Date();
    // // cookie
    date.setTime(date.getTime() - 10000);
    document.cookie = username + "=v; expires=" + date.toGMTString();
}
// /**/**
// * Cookie 
// * @param {string} cookieName Cookie 
// * @param {string} cookieValue Cookie 
// * @param {string} nDays Cookie 

function SetCookie(cookieName, cookieValue, nDays) {
    /**/
    /*  */
    var today = new Date();
    /**/
    /* Cookie  */
    var expire = new Date();
    /**/
    /* nDaysnDays0 1 */
    if (nDays == null || nDays == 0)
        nDays = 1;
    /**/
    /* Cookie */
    expire.setTime(today.getTime() + nDays * 24 * 60 * 60 * 1000); //      
    /**/
    /* Cookie */
    document.cookie = cookieName + "=" + escape(cookieValue) + ";expires=" + expire.toGMTString();
    //alert(cookieName + "=" + escape(cookieValue) + ";expires=" + expire.toGMTString());
}



